<template>
	<div>
		<div 
			class="item" 
			v-for="(item,index) of list"
			:key="index"
			>
			<div class="item-title border-bottom">
				<span class="item-title-icon"></span>
				{{item.title}}
			</div>
			<div v-if="item.children" class="item-children">
				<detail-list :list="item.children"></detail-list>
			</div>
			<!--递归组件-->
		</div>
	</div>
</template>

<script>
	export default {
		name: 'DetailList',
		//名字给递归组件用的
		props:{
			list: Array
		}
	
		
	}
</script>

<style scoped>
.item-title{
	line-height: .8rem;
	font-size: .32rem;
	padding: 0 .2rem;
}
.item-title-icon{
	position: relative;
	left: .07rem;
	top: .065rem;
	display: inline-block;
    width: .36rem;
    height: .36rem;
    background: url(http://s.qunarzz.com/piao/image/touch/sight/detail.png) 0 -.45rem no-repeat;
    margin-right: .1rem;
    background-size: .4rem 3rem;
}
.item-children{
	padding: 0 .2rem;
}
</style>


